async function saveData(data){

    const URL="http://localhost:8000/save",
          REQ_OBJ = {
            method:"POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify({text:data})
          }
    let req = await fetch(URL,REQ_OBJ)
    return req;
}


async function getData(){
    const URL="http://localhost:8000/todo",
          REQ_OBJ = {
            method:"GET",
            // method:"POST",
            headers: {
                'Content-Type': 'application/json'
            }
          }
    let req = await fetch(URL,REQ_OBJ)
    return req.json();
}

async function updateData(obj,deleteData = false){
    const URL="http://localhost:8000/todo",
          REQ_OBJ = {
            method:deleteData?"DELETE":"PUT",
            headers: {
                'Content-Type': 'application/json'
            },
            body:JSON.stringify(obj)
          }
    
    let req = await fetch(URL,REQ_OBJ)
    return req.json();
}

export {saveData,getData,updateData}
