import React from 'react'
import {saveData} from '../../data/data.js'
import css from './TodoForm.module.css' 

export default function TodoForm(props) {
    
    const [value,setValue] = React.useState("")

    const submitHandler = () => {
        saveData(value)
        .then(data=>{
            if(data.status === 200){
                props.updateTodo()
                setValue("")
            }
        })
        .catch(err=>console.log(err))
    }

    const handelInput = e =>{
        setValue(e.target.value)
    }

    const handleKeyEvent = e =>{
        if(e.key === "Enter"){
            submitHandler();
        }
    }

    return (
        <div>
            <input type="text" placeholder="Type Something" value={value} onInput={handelInput} onKeyDown={handleKeyEvent} className={`${css.input} ${css.common}`} />
            <button onClick={submitHandler} className={`${css.button} ${css.common}`} >Submit</button>
        </div>
    )
}
