import React from 'react'
import { updateData } from '../../data/data'
import css from './DisplayTodo.module.css'

export default function DisplayTodo(props) {
    const [disabled,setdisabled] = React.useState(true)
    const [value,setValue] = React.useState(props.text)
    
    
    const inputHandle = e=>{
        setValue(e.target.value)
    }
    
    const editBtnHandle = ()=>{
        if(!value){
            delBntHandle()
            return
        }
        if(!disabled){
            let obj = {
                key:props.index,
                data:value
            }
            updateData(obj)
            .then( res =>  console.log(res) )
            .then( ()=> setdisabled(!disabled) )
            .then( ()=> props.updateTodo() )
            .catch( (err)=> console.log(err) )
        }
        setdisabled(!disabled)
    }
    
    const delBntHandle = ()=>{
        let obj = {
            key:props.index,
            data:value
        }
        updateData(obj,true)
        .then( res =>  console.log(res) )
        .then( ()=> props.updateTodo() )
        .catch( (err)=> console.log(err) )
    }
    
    return (
        <div className={`${css.div}`}>
            <input type="text" value={value} onInput={inputHandle} disabled={disabled} className={`${css.input} ${css.common}`}></input>
            <div>
                <button className={`${css.button} ${css.common}`} onClick={editBtnHandle}>{disabled?"Edit":"Done"}</button>
                <button className={`${css.button} ${css.common}`} onClick={delBntHandle}>Delete</button>
            </div>
        </div>
    )
}
